import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../service/authentication.service';
import { Breakpoints, BreakpointObserver, BreakpointState } from '@angular/cdk/layout';
import { Customer, Employee, HttpClientService } from '../service/http-client.service';

@Component({
  selector: 'app-toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.css']
})
export class ToolbarComponent implements OnInit {
  collapse: boolean;
  accNo: string;
  constructor(private router: Router, private loginService: AuthenticationService, private breakpointObserver: BreakpointObserver,
    private httpService: HttpClientService) { 
    this.breakpointObserver
            .observe([Breakpoints.TabletPortrait, Breakpoints.Small, Breakpoints.XSmall])
            .subscribe((result: BreakpointState) => (this.collapse = result.matches));
  }

  ngOnInit() {
    if (sessionStorage.getItem('cusAccNo'))
      this.accNo = JSON.parse(sessionStorage.getItem('cusAccNo').toString());
    // console.log("test: "+ this.accNo);
    if (this.loginService.isEmployee === false) {
      this.httpService.getCustomer(this.accNo).subscribe(data => {
        this.httpService.customer = data;
      })
      this.httpService.getInterest(this.accNo).subscribe(data => {
        this.httpService.interest = data;
      })
    }
    
    
  }
  logout(){
    this.loginService.isLoggedIn = false;
    this.loginService.isEmployee = false;
    this.loginService.incompleteErrorCust = false;
    this.loginService.incompleteErrorEmp = false;
    this.loginService.showErrorCust = false;
    this.loginService.showErrorEmp = false;
    sessionStorage.removeItem('cusAccNo');
    sessionStorage.setItem('loggedIn', 'false');
    sessionStorage.setItem('loggedInAsEmployee', 'false');

    
    //location.reload();
  }

}
