import { Component, OnInit } from '@angular/core';
import { HttpClientService, Detail, Customer, Interest } from '../service/http-client.service';
import { AuthenticationService } from '../service/authentication.service';

@Component({
  selector: 'app-view-detail',
  templateUrl: './view-detail.component.html',
  styleUrls: ['./view-detail.component.css']
})
export class ViewDetailComponent implements OnInit {
  accNo: string;
  details: Detail;
  constructor(private httpClientService: HttpClientService, private loginService: AuthenticationService) { }

  ngOnInit() {
    var customer = new Customer('', '', '', '', '', '');
    var interest = new Interest('', '', '', '', '', '');
    this.details = new Detail(customer, interest);

    if (this.loginService.isEmployee === false) {
      this.accNo = sessionStorage.getItem('cusAccNo');
    }
  }

  getDetails(){
    if (this.accNo === undefined || this.accNo === '') {
      alert("Enter a valid Account No.");
      return;
    }
    else {
      this.httpClientService.getCustomer(JSON.parse(this.accNo)).subscribe(data => {
        if(data){ 
        this.details.customer = data;
        }
        else {
          alert("Enter a valid Account No.");
      return;
        }

        
      });
      this.httpClientService.getInterest(JSON.parse(this.accNo)).subscribe(data => {
        if (data)
          this.details.interest = data;
      });

    }

  }


}
