import { Component, OnInit } from '@angular/core';
import { HttpClientService, Customer, Interest } from '../service/http-client.service';
import { AuthenticationService } from '../service/authentication.service';

@Component({
  selector: 'app-view-accounts',
  templateUrl: './view-accounts.component.html',
  styleUrls: ['./view-accounts.component.css']
})
export class ViewAccountsComponent implements OnInit {

  customers: Customer[];
  displayedColumns: string[] = ['accNo', 'accBal', 'intRate', 'dailyInt', 'cumulativeInt', 'lastUpdated', 'op'];

  accounts: Interest[];

  constructor(private httpClientService: HttpClientService, private loginService: AuthenticationService) { }

  ngOnInit() {
    this.httpClientService.getAllAccounts().subscribe(
      response =>{this.accounts = response;}
     );
  }
  deleteCustomer(customer: Customer): void {
    this.httpClientService.deleteCustomer(customer)
      .subscribe( data => {
        alert('Customer deleted successfully.');
      })
  };

  deleteAccount(account: Interest): void {
    this.httpClientService.deleteAccount(account)
      .subscribe( data => {
        alert('Account deleted successfully.');
        window.location.reload();
      })
  };

}
