import { Component, OnInit } from '@angular/core';
import { HttpClientService } from '../service/http-client.service';
import { AuthenticationService } from '../service/authentication.service';

@Component({
  selector: 'app-deposit',
  templateUrl: './deposit.component.html',
  styleUrls: ['./deposit.component.css']
})
export class DepositComponent implements OnInit {
  
  accNo: string;
  amt: string;
  date: string;
  showError = false;
  noAccount = false;
  test: string;

  constructor(private httpService: HttpClientService, private loginService: AuthenticationService) { }

  ngOnInit() {
  }

  deposit() {
    this.test = new Date(Date.now()).toLocaleString('en-GB').split(',')[0];
    this.date = this.test.split('/')[0] + this.test.split('/')[1] + this.test.split('/')[2];

    if (this.accNo === undefined || this.amt === undefined) {
      this.showError = true;
      return;
    } else {
        this.showError = false;
        this.httpService.deposit(this.accNo, this.amt, this.date).subscribe(data => {
        if (data === false) {
          this.noAccount = true;
        } else {
          this.noAccount = false;
          alert('Amount deposited');
        }
      });
    }
  }

  


}
