import { Component, OnInit } from '@angular/core';
import { HttpClientService, Customer, Interest } from '../service/http-client.service';
import { AuthenticationService } from '../service/authentication.service';
@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.css']
})
export class AddCustomerComponent implements OnInit {
  user: Customer = new Customer('', '', '', '', '', '');
  accnt: Interest = new Interest('', '', '', '0', '0', undefined);
  showError = false;
  constructor(private httpClientService: HttpClientService, private loginService: AuthenticationService) { }

  ngOnInit() {
  }
  createCustomer(): void {
    if (this.user.accNo === '' || this.user.name === '' || this.user.panno === '' || this.user.aadhar === '' || this.user.address === '') {
      this.showError = true;
      return;
    }
    this.httpClientService.createCustomer(this.user).subscribe(data => {
      alert('Customer entered successfully.');
    });

  }

  createAccnt(): void {
    if (this.accnt.accBal === '' || this.accnt.accNo === '' || this.accnt.intRate === '') {
      this.showError = true;
      return;
    } else {
      this.httpClientService.new_accnt(this.accnt).subscribe(data => {
        if (data) {
          this.showError = false;
          alert('Account created successfully.');
        } else {
          alert('Either account exits or customer doesn\'t exist');
        }
      });
    }



  }
}
