import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
export class Customer{
  constructor(
    public accNo:string,
    public name:string,
    public address:string,
    public panno:string,
    public aadhar:string,
    public password:string,
  ) {}
}

export class Employee {
  constructor(
    public empId: string,
    public name: string,
    public address: string,
    public password: string,
  ){}
}

export class Interest {
  constructor(
    public accNo: string,
    public accBal: string,
    public intRate: string,
    public dailyInt: string, 
    public cumulativeInt: string,
    public lastUpdated: string
  ){}
}

export class Detail {
  constructor(
    public customer: Customer,
    public interest: Interest
  ){}
}

@Injectable({
  providedIn: 'root'
})
export class HttpClientService {
  customer = new Customer('', '', '', '', '', '');
  interest = new Interest('', '', '', '', '', '');

  constructor(private httpClient:HttpClient) { }
  getCustomers()
  {
    
    console.log("test call");
    return this.httpClient.get<Customer[]>('/customer');
  }

  getAllCustomers()
  {
    console.log("test call to all");
    return this.httpClient.get<Customer[]>('/bank/viewAll');
  }

  getAllAccounts() {
    return this.httpClient.get<Interest[]>('/bank/viewAllAccnts');
  }

  public deleteCustomer(customer) {
    return this.httpClient.delete<Customer>("/bank/deleteCustomer/" + customer.accNo);
  }

  public deleteAccount(account) {
    return this.httpClient.delete<Customer>("/bank/deleteAccount/" + account.accNo);
  }

  public createCustomer(customer) {
    console.log("test call to add");
    return this.httpClient.post<Customer>("/bank/addCustomer", customer);
  }

  public login(admin) {
    return this.httpClient.post<boolean>('/login/login', admin);
  }
  public login_emp(employee) {
    return this.httpClient.post<boolean>('/bank/login_emp', employee);
  }
  public login_cust(customer) {
    return this.httpClient.post<boolean>('/bank/login_cust', customer);
  }
  public signup_emp(employee) {
    return this.httpClient.post<boolean>('/bank/signup_emp', employee);
  }
  public signup_cust(customer) {
    return this.httpClient.post<boolean>('/bank/signup_cust', customer);
  }

  public deposit(accNo, amt, date) {
    return this.httpClient.get<boolean>('/bank/deposit/' + accNo + '/' + amt + '/' + date);
  }

  public withdraw(accNo, amt, date) {
    return this.httpClient.get<boolean>('/bank/withdraw/' + accNo + '/' + amt + '/' + date);
  }
  
  public new_accnt(interest) {
    return this.httpClient.post<boolean>('/bank/new_accnt', interest);
  }

  public getCustomer(accNo){
    return this.httpClient.post<Customer>('/bank/viewCustomerDetails', accNo)

  }

  public getInterest(accNo){
    return this.httpClient.post<Interest>('/bank/viewAccountDetails', accNo)

  }

  public getEmployee(empId){
    return this.httpClient.post<Employee>('/bank/viewEmployeeDetails', empId)
  }

}

