import { Component, OnInit } from '@angular/core';
import { HttpClientService } from './service/http-client.service';
import { Router } from '@angular/router';
import { AuthenticationService } from './service/authentication.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'retail-bank';
  accNo: string;
  constructor(private loginService: AuthenticationService, private httpService: HttpClientService) { }
  ngOnInit() {
    this.loginService.isLoggedIn = sessionStorage.getItem('loggedIn') === 'true';
    this.loginService.isEmployee = sessionStorage.getItem('loggedInAsEmployee') === 'true';
    if (this.loginService.isEmployee === false) {
      this.accNo = sessionStorage.getItem('cusAccNo').toString();

      // console.log("test: "+ this.accNo);
      if (this.loginService.isEmployee === false) {
        this.httpService.getCustomer(JSON.parse(this.accNo)).subscribe(data => {
          this.httpService.customer = data;
        })
        this.httpService.getInterest(JSON.parse(this.accNo)).subscribe(data => {
          this.httpService.interest = data;
        })
      }
    }

  }


}
