import { Component, OnInit } from '@angular/core';
import { HttpClientService, Customer } from '../service/http-client.service';
import { AuthenticationService } from '../service/authentication.service';
@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.css']
})
export class CustomerComponent implements OnInit {
  customers:Customer[];
  displayedColumns: string[] = ['name', 'accNo', 'panno', 'aadhar', 'address', 'op'];

  constructor(private httpClientService: HttpClientService, private loginService: AuthenticationService) { }

  ngOnInit() {
    this.httpClientService.getAllCustomers().subscribe(
      response =>{this.customers =response;}
     );
  }
  deleteCustomer(customer: Customer): void {
    this.httpClientService.deleteCustomer(customer)
      .subscribe( data => {
        alert('Customer deleted successfully.');
      })
  };
  /*handleSuccessfulResponse(response)
{

    this.customers=response;
    console.log(this.customers);

}*/
}
