import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClientService, Employee, Customer } from './http-client.service';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {
  public isLoggedIn = false;
  public isEmployee = false;
  public employee: Employee = new Employee('', '', '', '');
  public customer: Customer = new Customer('', '', '', '', '', '');
  public showErrorEmp = false;
  public showErrorCust = false;
  public incompleteErrorEmp = false;
  public incompleteErrorCust = false;
  ngOnInit() {
  }
  constructor(private router: Router,
    private httpClientService: HttpClientService) { }
  // public login() {
  //   this.httpClientService.login(this.admin).subscribe(data => {
  //     this.isLoggedIn = data;
  //     sessionStorage.setItem('loggedIn', 'true');
  //     if (data === false) {
  //       sessionStorage.setItem('loggedIn', 'false');
  //       this.showError = true;
  //     }
  //   });
  // }

  public login_emp() {
    this.httpClientService.login_emp(this.employee).subscribe(data => {
      if (data === true) {
        this.isLoggedIn = data;
        this.isEmployee = data;
        this.httpClientService.getEmployee(this.employee.empId).subscribe(data => {
          this.employee = data;
        });
        sessionStorage.setItem('loggedIn', 'true');
        sessionStorage.setItem('loggedInAsEmployee', 'true');
      } else {
        sessionStorage.setItem('loggedInAsEmployee', 'false');
        this.showErrorEmp = true;
      }
    });
    this.router.navigateByUrl('/');
  }

  public login_cust() {
    this.httpClientService.login_cust(this.customer).subscribe(data => {
      if (data === true) {
        this.httpClientService.getCustomer(this.customer.accNo).subscribe(data => {
          this.customer = data;
        });
        this.isLoggedIn = data;
        sessionStorage.setItem('loggedIn', 'true');
        sessionStorage.setItem('loggedInAsEmployee', 'false');
        sessionStorage.setItem('cusAccNo', this.customer.accNo);
      }
      else {
        this.showErrorCust = true;
      }
    });
    this.router.navigateByUrl('/');

  }

  public signup_emp() {
    if (this.employee.address === '' || this.employee.name === '' || this.employee.password === '' || this.employee.empId === '') {
      this.incompleteErrorEmp = true;
      return;
    }
    this.httpClientService.signup_emp(this.employee).subscribe(data => {
      if (data) {
        alert('Employee created');
        window.location.reload();
      }
      else {
        alert('Error in creating new employee');
      }

    });
  }
  public signup_cust() {
    if (this.customer.aadhar === '' || this.customer.accNo === '' || this.customer.address === ''
      || this.customer.name === '' || this.customer.panno === '' || this.customer.password === '') {
      this.incompleteErrorCust = true;
      return;
    }
    this.httpClientService.signup_cust(this.customer).subscribe(data => {
      if (data) {
        alert('Customer created');
        window.location.reload();
      }
      else {
        alert('Error in creating new employee');
      }
    });
  }
}
